body {
  background-color: #afb1b8;
  color: #212121;
  font-family: 'Roboto', sans-serif;
}

img {
  display: block;
  width: auto;
  max-width: 100%;
  height: 140px;
  margin: 0;
}

input {
  display: block;
  margin: 20px auto;
  background-color: #f5f4fa;
  border: 2px solid orangered;
  border-radius: 5px;
  min-width: 60px;
  font-size: 25px;
}
::placeholder {
  text-align: center;
  font-size: 25px;
}

.container {
  display: block;
  margin: 20px;
}

.gallery {
  display: grid;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: minmax(40px, auto);
  grid-gap: 1em;
}

.photo-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;
  height: 250px;
  background-color: #f5f4fa;
  padding: 5px;
  font-weight: 500;
  font-size: 14px;

  .stats {
    display: grid;
    justify-content: space-between;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: minmax(20px, auto);
    grid-gap: 0.5em;
    margin-top: 10px;
  }
  .stats-item {
    display: flex;
    margin: 0 auto;

    .material-icons {
      margin-right: 10px;
    }
  }
}

button {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 200px;
  height: 50px;
  padding: 0;
  border-radius: 4px;
  border: 2px solid orangered;
  margin-right: auto;
  margin-left: auto;
  min-width: 200px;

  background-color: #f5f4fa;
  color: #212121;
  font-size: 16px;
  line-height: 1.875;
  letter-spacing: 0.06em;
  cursor: pointer;
}

.is-hidden {
  display: none;
}